<template>
  <div class="My">
    <h3>{{userName}}22</h3>
    <van-button type="danger" size="large" class="MarginT_40" @click="LoginOut">退出登陆</van-button>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
// import {send} from '../util/send'
import { Dialog } from 'vant'
export default {
  name: 'My',
  data () {
    return {
    }
  },
  create () {
    alert('app---------------')
  },
  computed: {
    ...mapState({
      userName: state => state.userName
    })
  },
  methods: {
    ...mapActions([
      'changeCurTab',
      'changeCurPageName'
    ]),
    LoginOut () {
      Dialog.confirm({
        title: '提示',
        message: '确认退出当前账号？'
      }).then(() => {
        this.$router.push({name: 'Login'})
        this.changeCurTab(-1)
        this.changeCurPageName('Login')
      }).catch(() => {
        // on cancel
      })
    }
  }
}
</script>

<style scoped>
.My{
  padding: 20px;
}
</style>



