<template>
  <div id="app">
    <!-- <van-nav-bar
      title="梵家全屋定制智能订单系统"
      left-text=""
      right-text=""
    /> -->
    <router-view/>
    <van-tabbar v-model="curTab" v-if="curPageName !== 'Login'">
      <van-tabbar-item icon="records" v-show="role != 2">录入</van-tabbar-item>
      <van-tabbar-item icon="aim" v-show="role != 2">查询</van-tabbar-item>
      <van-tabbar-item icon="qr" v-show="role == 2">扫码</van-tabbar-item>
      <van-tabbar-item icon="contact">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script src="https://res.wx.qq.com/open/js/jweixin-1.2.0.js"></script>
<script>
import { mapState, mapActions } from 'vuex'
import {MySha1} from './util/utils'
export default {
  name: 'App',
  data () {
    return {
    }
  },
  computed: {
    ...mapState({
      curPageName: state => state.curPageName,
      role: state => state.role
    }),
    curTab: {
      get: function () {
        return this.$store.state.curTab
      },
      set: function (newValue) {
        this.changeCurTab(newValue)
      }
    }
  },
  watch: {
    curTab: function (val) {
      switch (val) {
        case 0:
          this.$router.push({name: 'Order'})
          break
        case 1:
          this.$router.push({name: 'Search'})
          break
        case 2:
          this.$router.push({name: 'Scan'})
          break
        case 3:
          this.$router.push({name: 'My'})
          break
      }
    }
  },
  created () {
    let HashString = window.location.hash
    if (HashString.indexOf('Order') !== -1) {
      this.changeCurTab(0)
      this.changeCurPageName('Order')
    } else if (HashString.indexOf('Search') !== -1) {
      this.changeCurTab(1)
      this.changeCurPageName('Search')
    } else if (HashString.indexOf('Scan') !== -1) {
      this.changeCurTab(2)
      this.changeCurPageName('Scan')
    } else if (HashString.indexOf('My') !== -1) {
      this.changeCurTab(3)
      this.changeCurPageName('My')
    } else {
      this.changeCurTab(-1)
      this.changeCurPageName('Login')
    }
    let mysignature = 'jsapi_ticket=HoagFKDcsGMVCIY2vOjf9h2cE5QpTC0wzRs_q1ISZAsMzPKn4bWPYWF1Un9PVBq2rY7cyQIATuxJXh0nfpeeKw&noncestr=wijncwjnkqk&timestamp=1543806013&url=http://baidu.com'
    console.log(MySha1(mysignature))
    // this.ConfigFn('HoagFKDcsGMVCIY2vOjf9h2cE5QpTC0wzRs_q1ISZAs3GFyBjbzBQ2NLj59QGnXS92sZOOvvwtMaHViatIghtQ')
  },
  methods: {
    ...mapActions([
      'changeCurPageName',
      'changeCurTab'
    ]),
    // 获取code
    ConfigFn (JsapiTicket) {
      var mytimestamp = (Date.parse(new Date())) / 1000
      var mynonceStr = sha1.hash(String(mytimestamp)).substring(0, 16)
      var mysignature = "jsapi_ticket=" + JsapiTicket + "&noncestr=" + mynonceStr + "&timestamp=" + mytimestamp + "&url=" + window.parent.location.href.split("#")[0]
      wx.config({
        beta: true,
        debug: false,
        appId: 'wwe507783c73f67620',
        timestamp: mytimestamp,
        nonceStr: mynonceStr,
        signature: sha1.hash(mysignature),
        jsApiList: [
          'scanQRCode',
          'chooseImage'
        ]
      })
      wx.error(function(res){
        console.log('config失败---')
      })
      wx.ready(function(){
      })
    }
  }
}
</script>

<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
}
.van-nav-bar{
  background: cadetblue !important;
  color: #fff;
}
</style>



