<template>
  <div class="Login">
    <!-- <van-cell-group> -->
      <van-field
        v-model="phoneNumber"
        required
        clearable
        label="手机号"
        placeholder="请输入手机号"
      />

      <van-field
        v-model="password"
        type="password"
        label="密码"
        placeholder="请输入密码"
        required
      />
    <van-button class="MarginT_40 loginBt" size="large" :loading='loading' round @click="Login2">登陆</van-button>

    <van-row type="flex" justify="space-between" class="MarginT_20">
      <van-col span="12" class="SmallSize"></van-col>
      <van-col span="12" class="SmallSize"><span @click="GoSign">还没有账户？去注册</span></van-col>
    </van-row>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import {send} from '../util/send'
import { Toast } from 'vant'
export default {
  name: 'Login',
  data () {
    return {
      loading: false,
      phoneNumber: '',
      password: ''
    }
  },
  created () {
  },
  computed: {
    // ...mapState({
    //   register_id: state => state.register_id
    // })
  },
  methods: {
    ...mapActions([
      'changeCurPageName',
      'changeCurTab'
    ]),
    Login2 () {
      // this.changeCurTab(2)
      // this.$router.push({name: 'Scan'})
      // this.changeCurPageName('Scan')
      this.changeCurTab(0)
      this.$router.push({name: 'Order'})
      this.changeCurPageName('Order')
    },
    GoSign () {
      this.$router.push({name: 'Sign'})
    },
    Login () {
      if (this.phoneNumber.trim() === '' || this.password.trim() === '') {
        Toast({
          duration: 1500,
          forbidClick: true,
          message: '手机号或密码不能为空!'
        })
        return false
      }
      this.loading = true
      send({
        name: '/userLoginPC?mobile=' + this.phoneNumber + '&fpassword=' + this.password,
        method: 'POST',
        data: {
        }
      }).then(_res => {
        switch (_res.data.code) {
          case 0:
            Toast.fail({
              duration: 1500,
              forbidClick: true,
              message: _res.data.message
            })
            this.loading = false
            break
          case 1:
            Toast.success('登陆成功！')
            this.loading = false
            this.$router.push({name: 'Order'})
            break
          default:
            Toast.fail('Interface error！')
            this.loading = false
        }
      }).catch((res) => {
        Toast.fail('Interface error！')
        this.loading = false
      })
    }
  }
}
</script>

<style scoped>
.Login{
  padding: 40px 20px;
}
.loginBt{
  background: cadetblue !important;
  color: #fff;
  height: 40px !important;
  line-height: 39px !important;
}
</style>



